import { React, useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';
import dayjs from 'dayjs';

// CSS
import st from '../../style/board/detail.module.css';

// 
import Header from '../common/Header';
import Footer from '../common/Footer';


const Detail = () => {
    const lg = console.log;
    const usp = useParams(); // /board/:idx    
    const navigate = useNavigate();

    // 
    const [state, setState] = useState({
        idx: usp?.idx,
        detailData: {
            idx: '',
            title: '',
            content: '',
            regdt: dayjs().format('YYYY-MM-DD'),
            regid: '1',
        },
    });

    const [selectedFile, setSelectedFile] = useState(null);  // :  

    //  
    const { idx, detailData } = state;


    //   
    const onChangeTitle = (e) => {
        setState(prevState => ({
            ...prevState,
            detailData: {
                ...detailData,
                title: e.target.value
            }
        }));
    };

    //  
    const onChangeContent = (e) => {
        setState(prevState => ({
            ...prevState,
            detailData: {
                ...detailData,
                content: e.target.value
            }
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        // API   
        navigate('/board/list');
    };


    //  
    const getBrdDtl = () => {
        const params = {
            idx
        }

        axios.post(`http://${process.env.REACT_APP_API_URL}/board/getBrdDtl`, params).then((res) => {
            lg(res.data[0])
            setState((prevState) => ({
                ...prevState,
                detailData: res.data[0]
            }));
        }).catch(() => {
            lg('')
        })
    }

    // 
    const postSave = () => {
        lg('postSave')
        const params = {
            title: detailData.title,
            content: detailData.content,
            regid: detailData?.regid || '1'
        }

        axios.post(`http://${process.env.REACT_APP_API_URL}/board/insertBrd`, params).then((res) => {
            lg('')
        }).catch(() => {
            lg('')
        })
    }

    // 
    const onCancel = () => {
        navigate(-1);
    };

    //    
    const handleFileChange = (e) => {
        setSelectedFile(e.target.files[0]);
    };

    useEffect(() => {
        getBrdDtl();
    }, [idx])

    return (
        <>
            <Header />
            <div className={st.mainWrap}>
                <div className={st.banner_content}>
                    <div className={st.form_section}>
                        <h2 className={st.form_title}>Comment </h2>
                        <div className={st.form}>
                            {/*    */}
                            <div className={st.info_group}>
                                <div className={st.info_item}>
                                    <label>:</label>
                                    <span>{detailData?.regid}</span>
                                </div>
                                <div className={st.info_item}>
                                    <label>:</label>
                                    <span>{dayjs(detailData?.regdt).format('YYYY MM DD HH:mm:ss')}</span>
                                </div>
                            </div>

                            <div className={st.input_group}>
                                <label htmlFor="title"></label>
                                <input
                                    type="text"
                                    id="title"
                                    name="title"
                                    value={detailData?.title}
                                    onChange={onChangeTitle}
                                    required
                                    className={st.input_field}
                                    placeholder=" "
                                />
                            </div>
                            <div className={st.input_group}>
                                <label htmlFor="content"></label>
                                <textarea
                                    id="content"
                                    name="content"
                                    value={detailData?.content}
                                    onChange={onChangeContent}
                                    required
                                    className={st.textarea_field}
                                    rows="15"
                                    placeholder=" "
                                />
                            </div>
                            {/*     */}
                            {/* <div className={st.input_group}>
                                <label htmlFor="file"></label>
                                <div className={st.file_upload_area}>
                                    <input
                                        type="file"
                                        id="file"
                                        onChange={handleFileChange}
                                        className={st.file_input}
                                    />
                                    {selectedFile && (
                                        <div className={st.file_name}>
                                            {selectedFile.name}
                                        </div>
                                    )}
                                </div>
                            </div> */}
                            <div className={st.button_group}>
                                <button className={st.submit_button} onClick={postSave}>
                                    
                                </button>
                                <button
                                    type="button"
                                    className={st.cancel_button}
                                    onClick={onCancel}
                                >
                                    
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    );
}

export default Detail;