import React, { useState } from 'react';
import Header from '../common/Header';
import Footer from '../common/Footer';
import { TextField, Button, Paper } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import st from '../../style/auth/signup.module.css';

const SignUp = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    username: '',
    password: '',
    passwordConfirm: '',
    representativeName: '',
    companyName: '',
    companyLocation: '',
    email: '',
    phone: ''
  });

  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const validateForm = () => {
    const newErrors = {};

    //   
    if (!formData.username) newErrors.username = ' ';
    if (!formData.password) newErrors.password = ' ';
    if (!formData.passwordConfirm) newErrors.passwordConfirm = '  ';
    if (formData.password !== formData.passwordConfirm) {
      newErrors.passwordConfirm = '  ';
    }
    if (!formData.representativeName) newErrors.representativeName = ' ';
    if (!formData.companyName) newErrors.companyName = ' ';
    if (!formData.companyLocation) newErrors.companyLocation = ' ';
    if (!formData.email) newErrors.email = ' ';
    if (!formData.phone) newErrors.phone = ' ';

    //   
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (formData.email && !emailRegex.test(formData.email)) {
      newErrors.email = '   ';
    }

    //   
    const phoneRegex = /^\d{2,3}-\d{3,4}-\d{4}$/;
    if (formData.phone && !phoneRegex.test(formData.phone)) {
      newErrors.phone = '    (: 010-1234-5678)';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validateForm()) {
      // TODO:   
      console.log(' :', formData);
    }
  };

  const textFieldStyle = {
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: '#1abc9c',
      },
      '&:hover fieldset': {
        borderColor: '#e74c3c',
      },
    },
    '& .MuiInputLabel-root': {
      color: '#ecf0f1',
    },
    '& .MuiOutlinedInput-input': {
      color: '#ecf0f1',
    },
  };

  return (
    <div className={st.mainWrap}>
      <Header />
      <div className={st.banner_content}>
        <div className={st.signup_container}>
          <Paper className={st.signup_paper}>
            <h2></h2>
            <form onSubmit={handleSubmit}>
              <TextField
                name="username"
                label=""
                variant="outlined"
                fullWidth
                margin="normal"
                value={formData.username}
                onChange={handleChange}
                error={!!errors.username}
                helperText={errors.username}
                sx={textFieldStyle}
              />
              <TextField
                name="password"
                label=""
                type="password"
                variant="outlined"
                fullWidth
                margin="normal"
                value={formData.password}
                onChange={handleChange}
                error={!!errors.password}
                helperText={errors.password}
                sx={textFieldStyle}
              />
              <TextField
                name="passwordConfirm"
                label=" "
                type="password"
                variant="outlined"
                fullWidth
                margin="normal"
                value={formData.passwordConfirm}
                onChange={handleChange}
                error={!!errors.passwordConfirm}
                helperText={errors.passwordConfirm}
                sx={textFieldStyle}
              />
              <TextField
                name="representativeName"
                label=""
                variant="outlined"
                fullWidth
                margin="normal"
                value={formData.representativeName}
                onChange={handleChange}
                error={!!errors.representativeName}
                helperText={errors.representativeName}
                sx={textFieldStyle}
              />
              <TextField
                name="companyName"
                label=""
                variant="outlined"
                fullWidth
                margin="normal"
                value={formData.companyName}
                onChange={handleChange}
                error={!!errors.companyName}
                helperText={errors.companyName}
                sx={textFieldStyle}
              />
              <TextField
                name="companyLocation"
                label=""
                variant="outlined"
                fullWidth
                margin="normal"
                value={formData.companyLocation}
                onChange={handleChange}
                error={!!errors.companyLocation}
                helperText={errors.companyLocation}
                sx={textFieldStyle}
              />
              <TextField
                name="email"
                label=""
                variant="outlined"
                fullWidth
                margin="normal"
                value={formData.email}
                onChange={handleChange}
                error={!!errors.email}
                helperText={errors.email}
                sx={textFieldStyle}
              />
              <TextField
                name="phone"
                label=""
                variant="outlined"
                fullWidth
                margin="normal"
                value={formData.phone}
                onChange={handleChange}
                error={!!errors.phone}
                helperText={errors.phone}
                sx={textFieldStyle}
              />
              <div className={st.button_group}>
                <button type="submit" className={st.submit_button}>
                  
                </button>
                <button
                  type="button"
                  className={st.cancel_button}
                  onClick={() => navigate('/')}
                >
                  
                </button>
              </div>
            </form>

          </Paper>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default SignUp;
